import dayjs from 'dayjs';
import { User } from "../../types/user";
import './header.sass';
import { calendar } from '../../mocks/calendar';
import { STATUSES } from '../../const';

type HeaderProps = {
  user: User;
  dateFrom: Date;
  dateTo: Date;
}

export const formatDate = (date: Date, format: string) =>
  dayjs(date).format(format);

export function Header({user, dateFrom, dateTo}: HeaderProps): JSX.Element {
  return (
    <header className="header">
      <div className="header__title-wrapper">
        <h1 className="header__title"> </h1>
      </div>
      <div className="header__content-wrapper">
        <p className="header__subtitle">, {user.firstName}</p>
        <div className="header__description-wrapper">
          <p className="header__description">
            <b>
               
            </b>
            !       : 
            {
              dateFrom.getMonth() === dateTo.getMonth()
                ? `${dateFrom.getDate()}-${formatDate(dateTo, 'DD/MM/YYYY')}`
                : `${formatDate(dateFrom, 'DD/MM')} - ${formatDate(dateTo, 'DD/MM/YYYY')}`
            }</p>
          <p className="header__description">
                     .
                 4  .
            <br/><b>
               !        .
                    .
            </b>
          </p>
        </div>
        <b className="header__description--summary">
            :&nbsp;
          {
            calendar.filter(
              (item) => item.status === STATUSES[1] || item.status === STATUSES[4]
            ).length
          }
         &nbsp; 
        </b>
      </div>
    </header>
  );
}
